function pad(num) {
    return ("0" + num).slice(-2);
}
/**
 * @return {string}
 */
export default function UserSeconds (seconds){
    if (!seconds){
        seconds = 0.1;
    }
    let minutes = Math.floor(seconds / 60);
    const outputSecs = Math.round(seconds % 60);
    let hours = Math.floor(minutes / 60);
    const outputMinutes = Math.round(minutes % 60);
    return `${pad(hours)}:${pad(outputMinutes)}:${pad(outputSecs)}`;
}