import React, {lazy, Suspense} from 'react';
import {Route, Switch, useRouteMatch} from 'react-router-dom';
import Loader from '@iso/components/utility/loader';

const routes = [
  {
    path: '',
    component: lazy(() => import('../Lince/Home')),
    exact: true,
  },
  {
    path: 'project',
    component: lazy(() => import('../Lince/ProjectInfo')),
  },
  {
    path: 'tool',
    component: lazy(() => import('../Lince/Tool')),
  },
  {
    path: 'scenes',
    component: lazy(() => import('../Lince/Scenes')),
  },
  {
    path: 'register',
    component: lazy(() => import('../Lince/Register')),
  },
  {
    path: 'results',
    component: lazy(() => import('../Lince/Results')),
  },
  {
    path: 'r-studio',
    component: lazy(() => import('../Lince/RStudio')),
  },
  {
    path: 'observers',
    component: lazy(() => import('../Lince/Observers')),
  },
  {
    path: 'authCheck',
    component: lazy(() => import('../AuthCheck')),
  },
  {
    path: 'inspector',
    component: lazy(() => import('../Lince/Inspector')),
  },
];

export default function AppRouter() {
  const { url } = useRouteMatch();
  return (
    <Suspense fallback={<Loader />}>
      <Switch>
        {routes.map((route, idx) => (
          <Route exact={route.exact} key={idx} path={`${url}/${route.path}`}>
            <route.component />
          </Route>
        ))}
      </Switch>
    </Suspense>
  );
}
