import React, {useEffect, useState} from 'react';
import PropTypes from "prop-types";
import {fetchSessionParameters} from '../../redux/lince/videoActions';
import {connect, useDispatch, useSelector} from 'react-redux';
import {Button, Drawer, Layout} from 'antd';
import {TinyHeader} from '../Lince/styled/LinceItems';
import appActions from '@iso/redux/app/actions';
import logo from '../../assets/images/lince/lince_icon-72x72.png';
import TopbarWrapper from './Topbar.styles';
import LinceLanguageSwitcher from "../LanguageSwitcher/LinceLanguageSwitcher";
import LinceSideBarContent from "../../components/lince/LinceSideBarContent";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEye} from "@fortawesome/free-solid-svg-icons";

const {Header} = Layout;
const {toggleCollapsed} = appActions;

function Topbar(props) {
    const [visible, setVisible] = useState(false);
    const customizedTheme = useSelector(state => state.ThemeSwitcher.topbarTheme);
    const {collapsed, openDrawer} = useSelector(state => state.App);
    const dispatch = useDispatch();
    const handleToggle = React.useCallback(() => dispatch(toggleCollapsed()), [
        dispatch,
    ]);
    useEffect(() => {
        dispatch(fetchSessionParameters());
    }, [dispatch]);
    const isCollapsed = collapsed && !openDrawer;
    const styling = {
        background: customizedTheme.backgroundColor,
        position: 'fixed',
        width: '100%',
        height: 70,
    };
    const showDrawer = () => {
        setVisible(true);
    };
    const onClose = () => {
        setVisible(false);
    };
    let multiObserverOptions = "";
    if (props.sessionParams && props.sessionParams.OBSERVERS > 1) {
        multiObserverOptions = <><TinyHeader title="Observador actual:" subTitle={props.sessionParams["OBSERVER_NAME"]}/>
            <Button type="primary" shape="round" size={"small"} onClick={showDrawer}>
                <FontAwesomeIcon icon={faEye}/>&#160;Cambiar observador
            </Button>
        </>
    }
    return (
        <TopbarWrapper>
            <Header style={styling} className={isCollapsed ? 'isomorphicTopbar collapsed' : 'isomorphicTopbar'}>
                <div className="isoLeft">
                    <button className={isCollapsed ? 'triggerBtn menuCollapsed' : 'triggerBtn menuOpen'}
                            style={{color: customizedTheme.textColor}}
                            onClick={handleToggle}/>
                    <img src={logo} alt="Lince plus icon" width={50}/>
                </div>
                <div className="isoRight">
                    {multiObserverOptions}
                    <LinceLanguageSwitcher/>
                </div>
            </Header>
            <Drawer title="Observers" placement="right" width={600} closable={true} onClose={onClose} visible={visible}>
                <LinceSideBarContent/>
            </Drawer>
        </TopbarWrapper>
    );
}

Topbar.propTypes = {
    sessionParams: PropTypes.object.isRequired,
    fetchSessionParameters: PropTypes.func.isRequired,
};
const mapStateToProps = state => ({
    sessionParams: state.lince.sessionParams
});
export default connect(mapStateToProps, {
    fetchSessionParameters
})(Topbar);