import React, {Component, forwardRef} from 'react';
import {connect} from 'react-redux';
import PropTypes from "prop-types";
import {fetchTool} from '../../redux/lince/videoActions';
import DataTable from "react-data-table-component";
import {Trans} from 'react-i18next';
import Moment from 'react-moment';
import UserSeconds from "../utility/UserSeconds";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import Tags from '@iso/components/uielements/tag';
import {faEye} from "@fortawesome/free-solid-svg-icons";


class LinceRegister extends Component {
    constructor(props) {
        super(props);
        this.state = {selected: -1};

    }

    componentWillMount() {
        this.props.fetchTool();
    }

    render() {
        let columns = [
            {
                name: <span><i className="fa fa-hourglass-o"/>&#160;T (sec)</span>,
                maxWidth: "40px",
                selector: 'videoTime', sortable: true, format: d => {
                    return UserSeconds(d.videoTime);
                }
            }, {
                name: <span><i className="fa fa-television"/> Frame</span>,
                maxWidth: "40px",
                hide: 'xs',
                selector: 'frames', sortable: true
            }];
        if (this.props.fullMode) {
            this.props.tool.forEach(v => {
                const currentId = v.id;
                columns.push({
                    name: <span>{v.code}</span>,
                    //sortable: true,
                    //selector: 'saveDate',
                    cell: row => {
                        let value = "";
                        row.register.forEach(currentRegister => {
                            if (currentRegister.parent === currentId) {
                                if (currentRegister.nodeInformation !== null) {
                                    value = currentRegister.nodeInformation.substring(0, 10);
                                    if (currentRegister.nodeInformation.length > 10) {
                                        value += "...";
                                    }
                                } else {
                                    value = currentRegister.code;
                                }
                            }
                        });
                        return <span>{value}</span>;
                    }
                });
            });
            columns.push({
                name: <span><i className="fa fa-calendar-check-o"/>&#160;<Trans i18nKey="date">Date</Trans></span>,
                hide: 'xs',
                sortable: true,
                selector: 'saveDate',
                cell: (row) => {
                    return <Moment format="DD/MM/YY HH:mm" parse="DD-MM-YYYY HH:mm:ss"
                                   date={row.saveDate}/>
                }

            });
        } else {
            columns.push({
                name: <span> <FontAwesomeIcon icon={faEye}/>&#160;<Trans
                    i18nKey="register">Register</Trans></span>,
                sortable: false,
                width: "400px",
                selector: 'register',
                cell: (row) => {
                    return <div style={{overflow: "initial!important;"}}>
                        {row.register.map(i => {
                            return <Tags key={i.code}>{i.code}</Tags>;
                        })}
                    </div>;
                }
            });
        }
        const RowExpansionItem = ({data})=>{
            return <div>
                <h6>{data.register.length} observaciones:</h6>
                {data.register.map(i => {
                    return <Tags key={i.code}>{i.code}</Tags>;
                })}
            </div>
        }
        return (
            <DataTable noHeader fixedHeader dense
                       responsive={true} expandableRows={true}
                       columns={columns}
                       data={this.props.register}
                       defaultSortField="videoTime"
                       expandableRowsComponent={<RowExpansionItem/>}
            />
        );
    }
}

LinceRegister.propTypes = {
    tool: PropTypes.array,
    register: PropTypes.array.isRequired,
    fullMode: PropTypes.bool,
    fetchTool: PropTypes.func.isRequired,
};
LinceRegister.defaultProps = {
    fullMode: true
};
const mapStateToProps = state => ({
    tool: state.lince.tool
});
const ConnectedLinceRegister = connect(mapStateToProps, {
    fetchTool: fetchTool
}, null, {forwardRef: true})(LinceRegister);
export default forwardRef((props, ref) =>
    <ConnectedLinceRegister {...props} myForwardedRef={ref}/>
);