import React, {Component} from 'react';
import PropTypes from "prop-types";
import {connect} from 'react-redux';
import {
    fetchProfileInfo,
    fetchRegisterById,
    fetchSessionParameters,
    setProfileRegister
} from '../../redux/lince/videoActions';

import {Trans} from 'react-i18next';
import {Button, Divider, List, Popconfirm, Statistic, Tag} from "antd";
import {faCheckCircle, faTh} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import LinceRegisterExternal from "./LinceRegisterExternal";

const activeScreen = {
    user: "USER", register: "REGISTER"
};

class LinceSideBarContent extends Component {

    constructor(props) {
        super(props);
        this.state = {activeScreen: activeScreen.user, user: {userName: ""}};
        this._setActiveScreen = this._setActiveScreen.bind(this);
        this.setProfileRegister = this.props.setProfileRegister.bind(this);
        this.fetchRegisterById = this.props.fetchRegisterById.bind(this);
    }

    componentWillMount() {
        this.props.fetchProfileInfo();
        this.props.fetchSessionParameters();
    }

    _setActiveScreen(e, user, param) {
        this.setState({user: user});
        let activeWindow;
        switch (param) {
            case activeScreen.register:
                this.fetchRegisterById(user.registerCode);
                activeWindow = activeScreen.register;
                break;
            case activeScreen.user:
                activeWindow = activeScreen.user;
                break;
            default:
                activeWindow = activeScreen.user;
                break;
        }
        this.setState({activeScreen: activeWindow});
    }

    _setActiveUser(e, param) {
        //const userName = this.state.user.userName;
        this.setProfileRegister(this.state.user.registerCode);
        this.setState({activeScreen: activeScreen.user});
    }

    _renderRegister() {
        const shownData = this.props.registerTemp;
        let output;
        if (shownData.length === 0) {
            output = <span className="name badge">
                <Trans i18nKey="sidePanel.nonObservations">Aún no tiene registros observados</Trans>
            </span>;
        } else {
            output = <LinceRegisterExternal register={shownData} fullMode={false}/>
        }
/*        const menu = (
            <Menu>
                <Menu.Item>
                    <Button>
                        Excel
                    </Button>
                </Menu.Item>
            </Menu>
        );*/
        return <div className="page-quick-sidebar-chat-user-messages">
            <Divider orientation="left" style={{color: '#333', fontWeight: 'normal'}}>Observación
                por {this.state.user.userName}</Divider>
            <div className="page-quick-sidebar-nav">
                <div className="input-group">
                    <div className="input-group-btn">
                        <button type="button" className="btn red"
                                onClick={(e) => this._setActiveScreen(e, {userName: ""}, activeScreen.user)}>
                            <i className="icon-paper-clip">&#160;</i>
                            <span><Trans i18nKey="back">Back</Trans></span>
                        </button>
                        <Popconfirm title="¿Quieres cambiar a este observador?"
                                    onConfirm={(e) => this._setActiveUser(e, activeScreen.user)}
                                    okText="Yes" cancelText="No">
                            <button type="button" className="btn green pull-right">
                                <Trans i18nKey="sidePanel.markActive">Marcar como activo</Trans>
                            </button>
                        </Popconfirm>
            {/*            <Dropdown overlay={menu} placement="bottomCenter">
                            <Button>Exportar</Button>
                        </Dropdown>*/}
                    </div>
                </div>
            </div>
            {output}
        </div>;
    }

    _renderUser(u) {
        let classActive = "";//"#42A5F5";
        if (u.registerCode === this.props.sessionParams.REGISTER) {
            classActive = <Tag color="green"><FontAwesomeIcon icon={faCheckCircle}
                                                              style={{fill: '#FEAC01'}}/>&#160;Selected</Tag>;
        }

        /*
                function cancel(e) {
                    console.log("dismiss");
                }
        */

        return <List.Item.Meta
            title={<>Researcher {u.userName}&#160;{classActive}</>}
            description={<><Statistic title={"Scenes"} value={u.registerAmount}
                                      prefix={<FontAwesomeIcon icon={faTh} style={{fill: '#43a047'}}/>}/>
                <Button onClick={(e) => this._setActiveScreen(e, u, activeScreen.register)}>
                    Mostrar
                </Button></>}
        />
    }

    render() {
        let users = [];
        if (this.props.profile.userProfiles) {
            users = this.props.profile.userProfiles;
        }
        let output;
        switch (this.state.activeScreen) {
            case activeScreen.register:
                output = this._renderRegister();
                break;
            case activeScreen.user:
            default:
                output = <List header={<div>Project users</div>} bordered dataSource={users}
                               renderItem={item => (
                                   <List.Item>
                                       {this._renderUser(item)}
                                   </List.Item>
                               )}/>;
        }
        return (
            <div className="page-quick-sidebar-chat-users" data-rail-color="#ddd"
                 data-wrapper-class="page-quick-sidebar-list">
                {output}
            </div>
        );
    }
}

LinceSideBarContent.propTypes = {
    sessionParams: PropTypes.object.isRequired,
    profile: PropTypes.object.isRequired,
    registerTemp: PropTypes.array,
    fetchSessionParameters: PropTypes.func.isRequired,
    fetchProfileInfo: PropTypes.func.isRequired,
    setProfileRegister: PropTypes.func.isRequired,
    fetchRegisterById: PropTypes.func.isRequired,
};
const mapStateToProps = state => ({
    sessionParams: state.lince.sessionParams,
    profile: state.lince.profile,
    registerTemp: state.lince.registerTemp
});
export default connect(mapStateToProps, {
    fetchSessionParameters,
    fetchProfileInfo,
    setProfileRegister,
    fetchRegisterById
})(LinceSideBarContent);
