import React, {forwardRef} from 'react';
import styled from 'styled-components';
import {Menu, PageHeader} from "antd";

/**
 * Doc importante!:
 Function components cannot be given refs. Attempts to access this ref will fail. Did you mean to use React.forwardRef()?
 solved: https://github.com/atomiks/tippyjs-react#component-children
 * @type {React.ComponentType<React.ClassAttributes<unknown>>}
 */
const CustomMenu = forwardRef((props, ref) => <Menu mode="horizontal"
                                                    ref={ref}
                                                    className={"font-green-sharp"}
                                                    theme={"dark"}
                                                    {...props}/>);
const LinceMenu = styled(CustomMenu)`
.font-green-sharp {
    color: #2ab4c0 !important;
}
`;
export const TinyHeader = styled(PageHeader)`
.ant-page-header-heading-title{
    font-size:12px!important;
}
.ant-page-header-heading-sub-title{
    font-size:12px!important;
}
`;


export default LinceMenu;
